.stars {
  display: flex;
}

.stars__star {
  background-size: center;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

html {
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header {
  width: 100%;
  box-shadow: 0 2px 4px #0000000d;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  margin-inline: 50px;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
}

.nav {
  height: 60px;
  display: flex;
}

.nav__list {
  align-items: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list-item .nav__item {
  text-align: center;
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.nav__list-item:first-child .nav__item {
  margin-left: 0;
}

.nav__list-item .nav__item:hover {
  color: #00acdc;
}

.nav__list-item .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  grid-gap: 46px 48px;
  grid-template-columns: 1fr;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.container {
